import React, { useEffect } from "react";
import clsx from "clsx";
import { makeStyles } from "@material-ui/core/styles";
import { useHistory } from "react-router-dom";

import {
  Divider,
  Grid,
  IconButton,
  ListItemIcon,
  Tooltip,
} from "@material-ui/core";
import GlobalState from "./GlobalState";
import { List, ListItem } from "@material-ui/core";

import ListItemText from "@material-ui/core/ListItemText";
import { getMenuRole, getMenuId } from "./MenuList";
import { border, borderBottom } from "@material-ui/system";

const useStyles = makeStyles((theme) => ({
  paper: {
    padding: theme.spacing(2),
    display: "flex",
    overflow: "auto",
    flexDirection: "column",
  },
  fixedHeight: {
    height: 240,
  },

  icon: {
    fontSize: "1rem",
    color: "#777",
    cursor: "pointer",
    paddingTop: "10px",
    paddingBottom: "8px",
    borderBottom: "1px solid #eee",
  },

  iconSelected: {
    color: "#fff",
    backgroundColor:  theme.palette.primary.main,
    transition: "all 0.1s ease-in-out"
  },

  menuText: {
    fontSize: "1rem",
    fontWeight: "500"
  },
}));

export default function MyMenu() {
  const classes = useStyles();
  const [state, setState] = React.useContext(GlobalState);

  const [selectedIndex, setSelectedIndex] = React.useState(0);

  let history = useHistory();

  useEffect(() => {
    setSelectedIndex(state.currentMenuIndex);
  }, [state.currentMenuIndex]);

  const handleListItemClick = (event, index) => {
    setSelectedIndex(index);
    history.push(`/${getMenuId(state.role,index)}`);
    setState((state) => ({ ...state, currentMenuIndex: index }));
  };

  return (
    <React.Fragment>
      <List>
        {state.role &&
          getMenuRole(state.role).map(
            (item) =>
              !item.hidden && (
                // <ListItem button selected={selectedIndex === item.index} onClick={(event) => handleListItemClick(event, item.index)}>
                // <ListItemIcon>
                //     {item.icon}
                // </ListItemIcon>
                // <ListItemText primary={`${item.title}`} />
                // </ListItem>
                <React.Fragment key={`${item.id}`}>
                  <div
                    key={`${item.id}`}
                    className={clsx(
                      classes.icon,
                      selectedIndex === item.index && classes.iconSelected
                    )}
                    onClick={(event) => handleListItemClick(event, item.index)}
                  >
                    <Grid
                      container
                      direction="row"
                      justify="flex-start"
                      alignItems="flex-start"
                      spacing={3}
                      style={{paddingLeft:"15px"}}
                    >
                      <Grid item>{item.icon}</Grid>

                      <Grid item style={{textAlign:"left"}}>
                        <span
                          className={classes.menuText}
                        >{`${item.title}`}</span>{" "}
                      </Grid>
                    </Grid>
                  </div>
                </React.Fragment>
              )
          )}
      </List>
    </React.Fragment>
  );
}
